@import url("https://fonts.googleapis.com/css2?family=Aclonica:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Acme:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Neue+Montreal:wght@500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Instagram+Sans+Condensed:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-aclonica: Aclonica;
  --font-acme: Acme;
  --font-neue-montreal: "Neue Montreal";
  --font-instagram-sans-condensed: "Instagram Sans Condensed";
  --font-heebo: Heebo;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-5xl: 24px;
  --font-size-xl: 20px;
  --font-size-15xl: 34px;
  --font-size-17xl: 36px;
  --font-size-29xl: 48px;
  --font-size-13xl: 32px;
  --font-size-xs: 12px;

  /* Colors */
  --color-gray-100: #050505;
  --color-white: #fff;
  --color-mediumpurple-100: #8f5db6;
  --color-whitesmoke: #f2f2f2;
  --color-blueviolet-100: #981ff6;
  --color-blueviolet-200: #892cd9;
  --color-gainsboro-100: #e2e2e2;
  --color-mediumorchid: #954fcd;
  --color-black: #000;

  /* Paddings */
  --padding-mid: 17px;
  --padding-13xl: 32px;
  --padding-8xl: 27px;

  /* border radiuses */
  --br-11xl: 30px;
  --br-19xl: 38px;
  --br-12xl-5: 31.5px;
}
